import Vue from 'vue'
import VueI18n from 'vue-i18n'
import en from '../assets/i18n/en'
import zh from '../assets/i18n/zh'

Vue.use(VueI18n)

// 
const messages = {
  'zh-cn': zh, //   
  'en': en  // 
}

export default new VueI18n({ 
  locale: "zh-cn", // set locale 
  messages: messages // set locale messages
})